li {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 180px;
  height: 200px;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.15);
  margin: 0 16px 16px 0;
  border-radius: 4px;
  overflow: hidden;
}

.id {
  margin-left: 1rem;
  padding: 1px;
}

.passid {
  text-align: center;
  font-size: auto;
  font-weight: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.name {
  text-align: center;
  font-size: 2em;
  font-weight: bolder;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.todo {
  text-align: center;
  font-size: auto;
  font-weight: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.bt-container {
  width: 100%;
  display: auto;
  text-align: center;
  flex-direction: column;
}
.bt-container button {
  width: 100%;
}

.green {
  background-color: rgb(146, 199, 101);
}

.red {
  background-color: rgba(187, 62, 62, 0.801);
}
